@tailwind base;
@tailwind components;
@tailwind utilities;

.main {
    display: grid;
    grid-template-areas:
    "posts buttons"
    "posts ads";
    grid-auto-rows: min-content;
}

.posts {
    grid-area: posts;
}

.buttons {
    grid-area: buttons;
}

.ads {
    grid-area: ads;
}

@media  (max-width: 640px) {
    .main {
        grid-template-areas:
        "buttons"
        "posts"
        "ads";
    }
}